.header{
    font-family: 'Impact','New Century Schoolbook',
     Georgia, 'New York', serif;
    font-weight : bold;
    font-size : 6.00em;
    text-align: center;
    margin-top: 3%;
    fill-opacity: initial;
}

.basic-info{
    margin-top: 3%;  
}

.basic-info-big-font{
    font-size : 24px;
    height: 40;
}
.avatar-uploader,
.avatar-uploader-trigger,
.avatar {
  width: 150px;
  height: 150px;
}
.avatar-uploader {
  display: block;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
}
.avatar-uploader-trigger {
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  color: #999;
}
